.jeg-elementor-kit-dashboard {
    --gutenverse-dashboard-border: #E3E4E6;
    --gutenverse-dashboard-primary: #3B57F7;

    .f-content__elements {
        .f-theme-content-control-wrapper {
            padding: 40px;
            margin: 0 !important;
            background-color: #FFFFFF;
            display: flex;
            flex-direction: column;
            gap: 56px;
        }

        .f-theme-content-header {
            .header-control {
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 20px;
            }
        }

        .button-able {
            display: flex;
            border-radius: 50px;
            border: 1px solid var(--gutenverse-dashboard-border);
            padding: 7px;

            a {
                cursor: pointer;
                color: #40505d;
                font-style: normal;
                font-weight: 400;
                padding: 6px 17px;
                font-family: "Inter";
                font-weight: 500;
                font-size: 14px;
                letter-spacing: 0%;
                text-align: center;
                vertical-align: middle;

                &.active {
                    border-radius: 50px;
                    background: linear-gradient(180deg, #A05DF7 0%, #6839A5 162.5%);
                    color: #ffffff;
                }
            }
        }

        .block-items {

            .f-toggle-control {
                background: transparent;
                border-radius: 50px;
                cursor: pointer;
                height: 20px;
                position: relative;
                width: 45px;
                border: 1px solid #ACBADE;

                .pin {
                    background-color: #ACBADE;
                    border-radius: 50%;
                    position: absolute;
                    left: 3.5px;
                    top: 2.5px;
                    width: 14px;
                    height: 14px;
                    transition: left ease 0.3s;
                }

                &::before {
                    font-family: "Inter";
                    font-weight: 500;
                    font-size: 9px;
                    line-height: 1;
                    align-items: center;
                    color: #ACBADE;
                    content: "OFF";
                    display: flex;
                    position: absolute;
                    text-align: right;
                    top: 50%;
                    transform: translate3d(0, -50%, 0);

                }

                &.off {
                    &:before {
                        content: "OFF";
                        right: 5px;
                    }
                }

                &.on {
                    &:before {
                        color: #9747FF;
                        content: "ON";
                        left: 7px;
                        right: auto;
                    }

                    .pin {
                        left: 26.5px;
                        background: #9747FF;

                    }
                }

                &:focus,
                &:active {
                    outline: none;
                }

                &::-moz-focus-inner {
                    border: 0;
                }
            }

            display: grid;
            grid-column-gap: 30px;
            grid-template-columns: repeat(auto-fill, calc((100% / 4) - 23px));

            @media screen and (min-width: 781px) and (max-width: 1200px) {
                grid-template-columns: repeat(auto-fill, calc((100% / 3) - 20px));
            }

            @media screen and (min-width: 601px) and (max-width: 780px) {
                grid-template-columns: repeat(auto-fill, calc((100% / 2) - 15px));
            }

            @media screen and (max-width: 600px) {
                grid-template-columns: repeat(auto-fill, 100%);
            }

        }

        .block-item {
            align-items: center;
            border-radius: 5px;
            border: 1px solid #D1DBF5;
            cursor: pointer;
            display: flex;
            margin-bottom: 20px;
            overflow: hidden;
            padding: 17.5px 15px;
            position: relative;

            .pro-button {
                background: linear-gradient(180deg, #FFB12D 0%, #F59E0B 100%);
                width: 45px;
                height: 20px;
                color: #fff;
                border-radius: 50px;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 2px;

                span {
                    font-family: "Inter";
                    font-weight: 500;
                    font-size: 9px;
                    line-height: 1;
                }

                svg {
                    width: 8px;
                }
            }
        }

        .block-info {
            align-items: center;
            display: flex;
            flex: 1 1;

            .element-icon {
                min-width: 24px;
                min-height: 24px;
                width: 24px;
                height: 24px;
                background: #A05DF7;
                mask-repeat: no-repeat;
                mask-position: center;
            }
        }

        .block-icon {
            align-items: center;
            display: flex;

            >svg {
                width: 24px;
                height: 24px;
            }
        }

        .block-title {
            color: #0F1538;
            font-family: "Inter", serif;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            letter-spacing: 0%;
            margin: 0 5px 0 15px;
        }

        .block-category {
            background-color: #F8F9FC;
            padding: 30px 15px;
            border-radius: 8px;

            &-info {
                align-items: center;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                gap: 10px;
                margin-bottom: 30px;

                >p {
                    color: #011627;
                    font-family: "Inter", serif;
                    font-size: 18px;
                    font-style: normal;
                    font-weight: 500;
                    line-height: 21px;
                }
            }
        }

        .block-notice {
            align-items: center;
            border-left: 4px solid #ffc909;
            border: 1px solid var(--gutenverse-dashboard-border);
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 59px;

            .icon {
                align-items: center;
                background-color: #fff9e2;
                display: flex;
                height: 42px;
                justify-content: center;
                width: 42px;
            }

            .info {
                align-items: center;
                display: flex;
                flex: 1 1;
                font-family: "Inter", serif;
                gap: 7px;
                padding: 0 15px;

                >div:nth-child(1) {
                    font-weight: bold;
                }

                >span:nth-child(2) {
                    background-color: var(--gutenverse-dashboard-border);
                    height: 2px;
                    width: 15px;
                }

                >div:nth-child(3) {
                    span {
                        color: var(--gutenverse-dashboard-primary);
                        cursor: pointer;
                        position: relative;
                        text-decoration: underline;

                        &:hover {
                            img {
                                opacity: 1;
                                visibility: visible;
                            }
                        }
                    }

                    img {
                        right: 0;
                        opacity: 0;
                        position: absolute;
                        top: 14px;
                        visibility: hidden;
                        width: 428px;
                        left: -145px;
                    }
                }
            }
        }

        .block-enable-disable-all {
            align-items: center;
            border-radius: 3px;
            border: 1px solid var(--gutenverse-dashboard-border);
            display: flex;
            padding: 2px;

            >* {
                color: #011627;
                cursor: pointer;
                font-family: "Inter", serif;
                font-size: 14px;
                font-style: normal;
                font-weight: 400;
                line-height: 14px;
                margin: 0;
                padding: 8px 16px;
            }

            >.active {
                background: var(--gutenverse-dashboard-primary);
                border-radius: 2px;
                color: #ffffff;
            }
        }
    }
}